
.search__input{
    display: flex;
    align-items: center;
    border: 1px solid lightgray;
    background: #ffffff;
    height: 30px;
    padding: 10px 20px  ;
    border-radius: 999px;
    width: 75vw;
    margin: 0 auto;
    margin-top: 40px;
    max-width: 560px;
}

.search__input > input{
    flex: 1 ;
    background: #ffffff;
    padding: 10px 20px;
    font-size: medium;
    border: none;
}
.search__input > input:focus{
    outline-width: 20px;
}
.search__inputIcon{
    color: rgb(0, 0, 0);

}
.search__buttons{
    margin-top: 30px;
    display: flex;
    justify-content: center;

}

.search__buttons button{
    margin: 5px ;
    padding: 7px 15px;
    background-color: #f7f8f8;
    border: 1px solid white;
    text-transform: inherit;
    color: #000000;
}
.search__buttons button:hover{
    box-shadow: 0 1px 1px rgba(0,0,0,0.1);
    background-image: -webkit-linear-gradient(top,#f8f8f8,#f1f1f1);
    background-color: #f8f8f8;
    border: 1px solid #c6c6c6;
    color: rgb(0, 0, 0);
    
}
.search__buttonsHidden{
    display: none !important;
}
